import { Button, Checkbox, Col, DatePicker, Form, Input, message, Modal, Select } from "antd";
import {
  CHILDREN,
  DATE_FORMAT,
  ENTER_TEXT,
  FORM_NAME,
  MODAL_ADD_BUTTON_TYPE,
  MODAL_BACK_BUTTON,
  SELECT_TEXT,
  VALIDATION_FAILED,
} from "constants/CommonConstants";
import { ADD_PROJECT_TITLE } from "constants/ManageProjects";
import useIntlMessage from "hooks/useIntlMessage";
import moment from "moment";
import { useState } from "react";
import projectService from "services/ProjectService";
import Utils from "utils";
import { ADD_ACCOUNT_TEXT, CANCEL_TEXT, UPDATE_TEXT } from "../../../../constants/ManageAccounts";
import {
  ADD_PROJECT_MESSAGE,
  ADD_SUCCESS,
  CONFIRM_MESSAGE,
  EDIT_PROJECT_CHECKBOX_DATE,
  EDIT_PROJECT_TITLE,
  FORM_FIELDS,
  SELECT_HIGHT,
  SELECT_LAYOUT,
  UPDATE_PROJECT_MESSAGE,
  UPDATE_SUCCESS,
} from "../../../../constants/ManageProjectsConstant";

const formItemLayout = {
  labelCol: { span: 8 },
  wrapperCol: { span: 14 },
};

const EditProjectForm = ({
  show,
  isEdit,
  onCancel,
  setShow,
  editProject,
  accountsList,
  cancelEdit,
  refetchProjectList,
}) => {
  const [errorMessage, setErrorMessage] = useState("");
  const { confirm } = Modal;
  const { Option } = Select;
  const [form] = Form.useForm();

  const [loading, setLoading] = useState(false);
  const [updateResourcesEndDates, setUpdateResourcesEndDates] = useState(false);
  const [hasChanges, setHasChanges] = useState(false);

  const ProjectStatus = ["Active", "On hold", "Proposed", "Completed"];
  const startDate = isEdit ? moment(editProject?.projectStartDate) : null;
  const endDate = isEdit ? moment(editProject?.projectEndDate) : null;
  const [projectStatus, setProjectStatus] = useState(isEdit?.status);
  const getMessage = useIntlMessage();

  const handleConfirm = () => {
    if (hasChanges) {
      confirm({
        title: CONFIRM_MESSAGE.value,
        centered: true,
        onOk() {
          clearValuesAndCancelEditProject();
        },
      });
    } else {
      clearValuesAndCancelEditProject();
    }
  };

  const clearValuesAndCancelEditProject = () => {
    cancelEdit();
    onCancel();
  };

  const handleApiResponse = response => {
    // 701 for Update success and 700 for Add success.
    try {
      setLoading(true);
      if (response?.statusCode === 701 || response?.statusCode === 700) {
        message.success(response.statusCode === 701 ? getMessage(UPDATE_SUCCESS.text) : getMessage(ADD_SUCCESS.text));
        clearValuesAndCancelEditProject();
        refetchProjectList();
      } else {
        setErrorMessage(response.description);
      }
    } catch (e) {
      console.error("Error in getting the profile information", e);
    } finally {
      setLoading(false);
    }
  };

  const saveData = async payload => {
    let response;
    try {
      setLoading(true);
      if (isEdit) {
        response = await projectService.updateProject(payload, {
          updateResourcesEndDates: updateResourcesEndDates,
        });
      } else {
        response = await projectService.addProject(payload);
      }
    } catch (e) {
      console.error("Error in getting the profile information", e);
    } finally {
      setLoading(false);
    }
    handleApiResponse(response);
  };

  const handleOk = async e => {
    try {
      setLoading(true);
      let formValues = await form.validateFields();
      formValues.projectStatus !== "Completed" && delete formValues.projectEndDate;
      confirm({
        title: editProject ? getMessage(UPDATE_PROJECT_MESSAGE.text) : getMessage(ADD_PROJECT_MESSAGE.text),
        centered: true,
        onOk: async () => {
          try {
            const payload = projectService.createPayloadForAPI(formValues, accountsList, editProject);
            await saveData(payload);
          } catch (error) {
            console.log(error);
          } finally {
            setLoading(false);
          }
        },
      });
    } catch (info) {
      console.log(VALIDATION_FAILED.value, info);
    }
  };

  let initials = {
    projectName: editProject?.projectName,
    accountName: editProject?.account,
    projectStatus: editProject?.status,
    projectStartDate: startDate,
    projectEndDate: endDate,
  };

  const handleCheckBox = () => {
    setUpdateResourcesEndDates(!updateResourcesEndDates);
    setHasChanges(true);
  };

  const disableBeforeDate = current => {
    const values = form.getFieldsValue();
    return current && current.isBefore(values?.projectStartDate || moment().startOf("month"));
  };
  return (
    <Modal
      visible={show}
      title={isEdit ? EDIT_PROJECT_TITLE.text : ADD_PROJECT_TITLE.text}
      onCancel={handleConfirm}
      onOk={handleOk}
      okButtonProps={{ loading: loading }}
      bodyStyle={{ overflowY: SELECT_HIGHT.text, maxHeight: SELECT_HIGHT.value }}
      footer={[
        <Button key={MODAL_BACK_BUTTON.value} onClick={handleConfirm}>
          {CANCEL_TEXT.text}
        </Button>,

        <Button key={MODAL_ADD_BUTTON_TYPE.value} onClick={handleOk} type={MODAL_ADD_BUTTON_TYPE.value}>
          {isEdit ? UPDATE_TEXT.text : ADD_ACCOUNT_TEXT.value}
        </Button>,
      ]}
    >
      <Form
        form={form}
        name={FORM_NAME.text}
        layout={SELECT_LAYOUT.text}
        onFieldsChange={() => {
          setHasChanges(true);
        }}
        {...formItemLayout}
        initialValues={isEdit ? initials : null}
      >
        <Form.Item
          name={FORM_FIELDS.PROJECT_NAME.name}
          label={getMessage(FORM_FIELDS.PROJECT_NAME.label)}
          rules={[
            {
              required: isEdit && editProject?.isBenchProject ? false : true,
            },
          ]}
        >
          {isEdit && editProject?.isBenchProject ? (
            <span className="profile-text-color">{editProject?.projectName}</span>
          ) : (
            <Input
              placeholder={getMessage(ENTER_TEXT.label, FORM_FIELDS.PROJECT_NAME.label)}
              //   disabled={isEdit && editProject?.isBenchProject}
            />
          )}
        </Form.Item>
        <Form.Item
          name={FORM_FIELDS.ACCOUNT_NAME.name}
          label={getMessage(FORM_FIELDS.ACCOUNT_NAME.label)}
          rules={[
            {
              required: isEdit ? false : true,
            },
          ]}
        >
          {isEdit ? (
            <span className="profile-text-color">{editProject?.account}</span>
          ) : (
            <Select
              //  disabled={isEdit}
              showArrow
              showSearch
              optionFilterProp={CHILDREN}
              placeholder={getMessage(SELECT_TEXT.label, FORM_FIELDS.ACCOUNT_NAME.label)}
            >
              {Utils.sortArray(accountsList, FORM_FIELDS.ACCOUNT_NAME.name).map(e => {
                return (
                  <Option key={e.accountId} value={e.accountId}>
                    {e.accountName}
                  </Option>
                );
              })}
            </Select>
          )}
        </Form.Item>
        <Form.Item
          name={FORM_FIELDS.PROJECT_STATUS.name}
          label={getMessage(FORM_FIELDS.PROJECT_STATUS.label)}
          rules={[
            {
              required: true,
            },
          ]}
        >
          <Select
            placeholder={getMessage(SELECT_TEXT.label, FORM_FIELDS.PROJECT_STATUS.label)}
            onSelect={e => setProjectStatus(e)}
          >
            {Utils.sortArray(ProjectStatus).map(e => {
              return (
                <Option key={e} value={e}>
                  {e.charAt(0).toUpperCase() + e.slice(1)}
                </Option>
              );
            })}
          </Select>
        </Form.Item>

        <Form.Item
          label={getMessage(FORM_FIELDS.PROJECT_START_DATE.label)}
          name={FORM_FIELDS.PROJECT_START_DATE.name}
          rules={[
            {
              required: true,
              message: getMessage(FORM_FIELDS.PROJECT_START_DATE.errorMessage),
            },
          ]}
        >
          <DatePicker format={DATE_FORMAT} className="w-100" />
        </Form.Item>
        {((isEdit && projectStatus === "Completed") || initials.projectStatus === "Completed") && (
          <Form.Item
            label={getMessage(FORM_FIELDS.PROJECT_END_DATE.label)}
            name={FORM_FIELDS.PROJECT_END_DATE.name}
            rules={[
              {
                required: true,
                message: getMessage(FORM_FIELDS.PROJECT_END_DATE.errorMessage),
              },
            ]}
          >
            <DatePicker format={DATE_FORMAT} className="w-100" disabledDate={disableBeforeDate} />
          </Form.Item>
        )}
        {editProject && (
          <Checkbox
            className="d-flex text-left ml-5"
            name="updateResourcesEndDates"
            value={updateResourcesEndDates}
            onChange={() => handleCheckBox()}
          >
            {EDIT_PROJECT_CHECKBOX_DATE.text}
          </Checkbox>
        )}
      </Form>

      <Col>
        {errorMessage && (
          <div className="text-left my-3" style={{ color: "red" }}>
            * {errorMessage}
          </div>
        )}
      </Col>
    </Modal>
  );
};

export default EditProjectForm;
